import React, { useState } from 'react';
import './index.css'
import { LineChart, Line, XAxis, YAxis, Legend, Tooltip, CartesianGrid} from 'recharts';


const Encription = () => {

    const [value1,setValue1] = useState([0,0,0,0,0,0,0,0])
    const [value2,setValue2] = useState([0,0,0,0,0,0,0,0])
    const [value3,setValue3] = useState([0,0,0,0,0,0,0,0])
    const [value4,setValue4] = useState([0,0,0,0,0,0,0,0])
    const [value5,setValue5] = useState([0,0,0,0,0,0,0,0])
    const [value6,setValue6] = useState([0,0,0,0,0,0,0,0])
    const [value7,setValue7] = useState([0,0,0,0,0,0,0,0])
    const [value8,setValue8] = useState([0,0,0,0,0,0,0,0])

    const [val1,setVal1] = useState(0)
    const [val2,setVal2] = useState(0)
    const [val3,setVal3] = useState(0)

    let [key,setKey] = useState([])


    let stroka1
    let stroka2
    let stroka3
    let stroka4
    let stroka5 =[]
    let stroka6
    let stroka7 =[]
    let stroka8


    const data = [
    {время: '100', 'правильно принятые фотоны': 1, 'неправильно принятые фотоны': 1},
    {время: '200', 'правильно принятые фотоны': 2, 'неправильно принятые фотоны': 3},
    {время: '300', 'правильно принятые фотоны': 4, 'неправильно принятые фотоны': 6},
    {время: '400', 'правильно принятые фотоны': 2, 'неправильно принятые фотоны': 2},];   


    function keyCount() {

        let stroka11 = []
        let stroka21 = []
        let stroka31 = []
        let stroka41 = []
        let stroka51 = []
        let stroka61 = []
        let stroka71 = []
        let stroka81 = []
        let n = 256000
        for(let i = 0; i<1; i++) {
            stroka11[i] = 0
            stroka21[i] = 0
            stroka31[i] = 0
            stroka41[i] = 0
            stroka51[i] = 0
            stroka61[i] = 0
            stroka71[i] = 0
            stroka81[i] = 0
        }
        let count1 = 0
        let count2 = 0
        
        let b = Date.now()
        for(let i = 0; i<n; i++) {
            if(count1 > 256000) {break}
            stroka11 = value1.map((i)=> {
                let a = Math.trunc(Math.random()*2)
                return a === 0? 0 : 'π/2'
            })
            
            stroka21 = stroka11.map((i)=> i === 'π/2'? 1 : 0)
            stroka31 = stroka21.map((i)=> {
                let a = Math.trunc(Math.random()*2)
                return a === 0? 0 : '3π/2'
            })

            stroka41 = stroka31.map((i)=> i === '3π/2'? 1 : 0)
            for(let i = 0; i<8; i++) {
                if(stroka11[i] === 0 && stroka31[i] === 0) {
                    stroka51.push(0)
                }
                if(stroka11[i] === 'π/2' && stroka31[i] === 0 || stroka11[i] === 0 && stroka31[i] === '3π/2') {
                    stroka51.push('π/2')
                }
                if(stroka11[i] === 'π/2' && stroka31[i] === '3π/2') {
                    stroka51.push('π')
                }
            }
            let stroka61 = stroka51.map((i)=> i === 0 ? '100%' : i === 'π'? 0 : '50%' )
            let stroka71 = stroka51.map((i)=> i === 0 ? 0 : i === 'π'? '100%' : '50%' )
            let stroka81 = stroka71.map((i)=> i === 0 ? 0 : i === '100%' ? 1 : '-' )
            
            
            stroka81.map((i)=> i === 0 ? count1++ : i === '100%' ? count1++  : count2++)
           if(count1 > 256000) {
               key=stroka81.filter((i) => i =='-')
               key.length = 256000 
            setKey(stroka81)
             
            break}

        }
        setVal1(count1)
        setVal2(count2*1000)
        setVal3((count1+count2)/800)



        var k_total = 10000; // number of times the event is repeated
var landa = 0.00000003;  // Promedian number of error expected in a given time (Landa symbol)
var exponential = 2.718281828;
var total = 0;
var numerator, denominator;



function poisson(k, landa) {
   let exponentialPower = Math.pow(exponential, -landa); // negative power k
  let  landaPowerK = Math.pow(landa, k); // Landa elevated k
  let  numerator = exponentialPower * landaPowerK;
  let  denominator = fact(k); // factorial of k.
    
    return (numerator / denominator);
}

for (var i = 0; i < k_total; i++) {
    total += poisson(i, landa);
}

console.log("Total sum is " + total);

function fact(x) {
   if(x==0) {
      return 1;
   }
   return x * fact(x-1);
}


    }

    function generateKey() {
        stroka1 = value1.map((i)=> {
            let a = Math.trunc(Math.random()*2)
            return a === 0? 0 : 'π/2'
        })
        setValue1(stroka1)
        stroka2 = setTimeout(()=> setValue2(stroka1.map((i)=> i === 'π/2'? 1 : 0)),300)
        stroka3 = value3.map((i)=> {
            let a = Math.trunc(Math.random()*2)
            return a === 0? 0 : '3π/2'
        })
        setTimeout(()=>setValue3(stroka3),600)
        stroka4 = setTimeout(()=> setValue4(stroka3.map((i)=> i === '3π/2'? 1 : 0)),900)


        for(let i = 0; i<8; i++) {
            if(stroka1[i] === 0 && stroka3[i] === 0) {
                stroka5.push(0)
            }
            if(stroka1[i] === 'π/2' && stroka3[i] === 0 || stroka1[i] === 0 && stroka3[i] === '3π/2') {
                stroka5.push('π/2')
            }
            if(stroka1[i] === 'π/2' && stroka3[i] === '3π/2') {
                stroka5.push('π')
            }
        }
        setTimeout(()=>setValue5(stroka5),1200)
        stroka6 = setTimeout(()=> setValue6(stroka5.map((i)=> i === 0 ? '100%' : i === 'π'? 0 : '50%' )),1500)
        stroka7 = stroka5.map((i)=> i === 0 ? 0 : i === 'π'? '100%' : '50%' )
        setTimeout(()=> setValue7(stroka7),1800)
        stroka8 = setTimeout(()=> setValue8(stroka7.map((i)=> i === 0 ? 0 : i === '100%' ? 1 : '-' )),2100)
    }
    return (
        <>
        <h4>Распределение/передача ключа по протоколу B92</h4>
        <button onClick={generateKey}> сгенерировать ключ</button>
        <div className='enc'>
            <div className='enc_content title'>Фазовый сдвиг отправителя Алиса ФA</div>
                {value1.map((i) => <div className='enc_content'>{i}</div>)}
            <div className='enc_content title'>Значения бита отправителя Алиса</div>
                {value2.map((i) => <div className='enc_content'>{i}</div>)}
            <div className='enc_content title'>Фазовый сдвиг получателя Боб ФB</div>
                {value3.map((i) => <div className='enc_content'>{i}</div>)}
            <div className='enc_content title'>Значения бита получателя Боб</div>
                {value4.map((i) => <div className='enc_content'>{i}</div>)}
            <div className='enc_content title'>Разность фаз ФA-ФB</div>
                {value5.map((i) => <div className='enc_content'>{i}</div>)}
            <div className='enc_content title'>Вероятность приёма фотона 1-м однофотонным фотодетектором </div>
                {value6.map((i) => <div className='enc_content a'>{i}</div>)}
            <div className='enc_content title'>Вероятность приёма фотона 2-м однофотонным фотодетектором</div>
                {value7.map((i) => <div className='enc_content a'>{i}</div>)}
            <div className='enc_content title'>Бит ключа</div>
                {value8.map((i) => <div className={(i==='-') ? 'enc_content c': 'enc_content b'}>{i}</div>)}
        
        </div>
        <button onClick={keyCount}> сгенерировать ключ 256кб</button>
        <h4> количество правильно принятых фотонов = {val1}</h4>
        <h4> количество неправильно принятых фотонов = {val2}</h4>
        <h4> ключ сгенерирован за {val3} с</h4>
        <h4> количество ошибочно принятых фотонов = {Math.trunc(val2*Math.random()*0.1)}</h4>
        <LineChart
                    width={400}
                    height={400}
                    data={data}
                    margin={{ top: 5, right: 20, left: 10, bottom: 5 }}
                    >
                        
                     <XAxis dataKey="время" />
                     <Legend />
                    <Tooltip />
                    <CartesianGrid stroke="#f5f5f5" />
                    <Line type="monotone" dataKey="правильно принятые фотоны" stroke="#111111" yAxisId={1} />
                    <Line type="monotone" dataKey="неправильно принятые фотоны" stroke="#ff7300" yAxisId={1} />
                </LineChart>

          <div style={{margin: '10px', padding: '10px'}}>  ключ = {key} </div>
        </>
    );
};

export default Encription;